<template>
	<view>
		<view class="gui-margin-top gui-bg-#999999; demo-nav2">
			<gui-switch-navigation :items="[{id:0,name:'全部'},{id:1,name:'收入'},{id:2,name:'提现'}]" @change="navchange"
				textAlign="center" :isCenter="true" activeDirection="center" :size="0"
				activeLineBg="linear-gradient(to right,black , black)" activeColor="black" :margin="40" padding="60rpx"
				activeLineHeight="4rpx" lineHeight="72rpx" activeLineWidth="100rpx"></gui-switch-navigation>
		</view>
	</view>
</template>
<script>
	var graceJS = require('@/GraceUI5/js/grace.js');
	export default {
		data() {
			return {
				navItems: []
			}
		},
		methods: {
			navchange: function(index) {
				console.log(index);
			}
		},
		onLoad: function() {
			// api 加载导航数据
			graceJS.get(
				'https://www.graceui.com/api/tabs/forV5', {}, {},
				(res) => {
					this.navItems = res.data;
					console.log(res)
				}
			);
		}
	}
</script>
